import { useState } from "react";

const App = () => {
  const [todos, setTodos] = useState([]);
  const [tmpTodo, setTmpTodo] = useState("");

  const addTodo = () => {
    if (tmpTodo === "") {
      alert("文字を入力してください");
      return;
    }
    setTodos([...todos, tmpTodo]);
    setTmpTodo("");
  };

  return (
    <>
      <h1>Todo App</h1>
      <input
        type="text"
        name="todo"
        onChange={e => setTmpTodo(e.target.value)}
        value={tmpTodo}
      />
      <button onClick={addTodo}>Add</button>
      <ul>
        {todos.map(todo => {
          return <li>{todo}</li>;
        })}
      </ul>
    </>
  );
};

export default App;
